@import 'https://fonts.googleapis.com/css?family=Share+Tech+Mono';
@font-face {
	font-family: 'Digital';
	src: url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot');
	src: url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot?#iefix')
			format('embedded-opentype'),
		url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff2')
			format('woff2'),
		url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff')
			format('woff'),
		url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.ttf')
			format('truetype'),
		url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.svg#Digital-7')
			format('svg');
}

.display-container {
	position: relative;
	min-height: 8rem;
	border: 1px solid black;
	border-radius: 5px;
	font-family: 'Digital', monospace;
	text-align: right;
	padding: 0.5rem 1rem;
	background-color: rgb(73, 233, 73);
	box-shadow: 0 0 5px 5px rgba(73, 233, 73, 0.25);
	color: rgb(32, 32, 32);
}

.display-big {
	font-size: 4.5rem;
	font-weight: bold;
	margin-top: 0.5rem;
	position: absolute;
	bottom: 0;
	right: 1rem;
}

.display-small {
	font-size: 2.5rem;
	position: absolute;
	top: 0.5rem;
	right: 1rem;
}

@media screen and (max-width: 280px) {
	.display-big {
		font-size: 3.5rem;
		bottom: 0.5rem;
	}

	.display-small {
		font-size: 2.5rem;
		top: 0.5rem;
	}
}
